header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  margin: 0 5px 10px;
}

header h2 {
  font-family: Regular;
  font-weight: normal;
  font-size: 13px;
  color: var(--subtext);
}

.theme-switch {
  position: relative;
  width: 64px;
  height: 32px;
  border-radius: 32px;
  transition: 0.4s;
  background-color: var(--primary100);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1), inset 0 0px 5px rgba(0, 0, 0, 0.5);
}

#theme-switch__indicator {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: linear-gradient(to bottom, var(--primary400), var(--primary100));
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3), inset 0 0 2px rgba(255, 255, 255, 0.2);
  transition: 0.4s;
}

#dark #theme-switch__indicator {
  transform: scale(0.92) translateX(0);
}

#light #theme-switch__indicator {
  transform: scale(0.92) translateX(109%);
  background: linear-gradient(to bottom, var(--primary100), var(--primary500));
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5), inset 0 0 2px rgba(0, 0, 0, 0.1);
}

.theme-switch__indicator__icon {
  color: #f3f3f3;
}
